html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

*:focus {
  outline: 1px solid var(--cta-color);
}

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

._container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn-banner {
  color: var(--light-color);
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 125%;
  text-transform: uppercase;
  border-radius: 4px;
  background: linear-gradient(88deg, #223E4E -34.89%, #0091A4 123.19%);
  transition: all 0.3s ease 0s;
  position: relative;
  z-index: 1;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--accent-color);
    z-index: -1;
    transition: all 0.3s ease 0s;
    opacity: 0;
  }
  &:hover {
    box-shadow: 0px 6px 16px 0px rgba(22, 27, 46, 0.20), 0px 2px 4px 0px rgba(22, 27, 46, 0.16);
    &:after {
      opacity: 1;
    }
  }
  &:focus {
    outline: 1px solid var(--cta-color);
    background: var(--accent-color);
    box-shadow: 0px 6px 16px 0px rgba(22, 27, 46, 0.10), 0px 2px 4px 0px rgba(22, 27, 46, 0.08), 0px 0px 48px 0px rgba(22, 27, 46, 0.10) inset;
  }
  &:active {
    background: var(--accent-color);
    box-shadow: 0px 0px 52px 0px rgba(22, 27, 46, 0.50) inset;
  }
}

.btn {
  color: var(--light-color);
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 125%;
  text-transform: uppercase;
  border-radius: 4px;
  background-image: linear-gradient(88deg, #E24C4F -34.89%, #FF8781 123.19%);
  display: inline-block;
  transition: all 0.5s ease 0s;
  position: relative;
  overflow: hidden;
  z-index: 1;
  &:after {
    content: '';
    position: absolute;
    left: -20%;
    top: 0;
    border-radius: 50px;
    width: 0%;
    height: 100%;
    background-color: #d24038;
    z-index: -1;
    transition: all 0.5s ease 0s;
  }
  &:hover {
    box-shadow: 0 0 15px 1px rgba(#d24038, 0.4);
    &:after {
      width: 140%;
    }
  }
}

.title-top {
  color: var(--accent-color);
  font-family: 'Inter', sans-serif;
  font-size: 72px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  @media (max-width: 992px){
      font-size: 55px;
  }
  @media (max-width: 768px){
      font-size: 44px;
  }
}
