@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

.all_pages {
  position: fixed;
  top: 20%;
  right: 0;
  z-index: 101;
  &:hover {
    ul {
      right: 0;
    }
  }
  .page_point {
    padding: 10px 15px;
    background: #00d2ff;
    cursor: pointer;
    color: #fff;
  }
  ul {
    background: #00d2ff;
    padding: 15px 20px;
    position: absolute;
    top: 100%;
    width: 200px;
    right: -200px;
    transition: 0.4s;
    max-height: 450px;
    overflow-y: auto;
    li {
      list-style: none;
      margin-bottom: 15px;
      font-size: 16px;
      a {
        color: #fff;
      }
    }
  }
}

body {
  &.lock {
    overflow: hidden;
  }
}

.wow {
  visibility: hidden;
}
// Header start ========================================================================================================================================================

.header {
  padding: 27px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  margin: 0 auto;
  transition: all .3s ease 0s;
  @media (max-width: 992px){
      padding: 10px 0;
  }
  &.scroll {
    box-shadow: 0px 0px 6px 2px rgba(#086470, 0.4);
    background-color: rgba(#086470, 0.9);
    padding: 10px 0;
    .header__logo, .nav__item, .header__btn {
      visibility: visible !important;
    }
    @media (max-width: 992px){
      .header__logo.active {
        svg {
          path {
            fill: var(--accent-color);
          }
        }
      }
    }
    .header__logo {
      svg {
        transition: all 0.3s ease 0s;
        path {
          fill: var(--light-color);
        }
      }
    }
    .burger {
      color: var(--light-color);
    }
    @media (min-width: 993px){
      nav > ul > li > a {
        color: var(--light-color);
      }
      nav > ul > li > ul + svg {
        path {
          stroke: var(--light-color);
        }
      }
    }
  }

  // .header__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 992px){
        > .header__btn {
          width: 0;
          height: 0;
          padding: 0;
        }
    }
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 100;
    @media (max-width: 576px){
        svg {
          width: 150px;
        }
    }
    @media (max-width: 400px){
      svg {
        width: 120px;
      }
    }
  }

  // .header__btn

  &__btn {
    padding: 16px 24px;
    font-size: 14px;
    @media (max-width: 992px){
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 10%;
    }
    @media (max-height: 320px){
        padding: 10px 20px;
        bottom: 5%;
    }
  }

  nav {
    > ul {
      display: flex;
      column-gap: 128px;
      @media (max-width: 1400px){
        column-gap: 90px;
      }
      @media (max-width: 1200px){
        column-gap: 70px;
      }
      @media (max-width: 992px){
          flex-direction: column;
          align-items: center;
          justify-content: center;
          text-align: center;
      }
      > li {
        @media (max-width: 992px){
            margin-bottom: 10px;
        }
        > a {
          color: var(--accent-color);
          font-family: 'Inter', sans-serif;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 125%;
          text-transform: uppercase;
          position: relative;
          @media (min-width: 993px){
              &.active {
                &:after {
                  content: '';
                  position: absolute;
                  left: 0;
                  bottom: -5px;
                  width: 100%;
                  height: 2px;
                  background-color: var(--orange-color);
                  transition: all 0.3s ease 0s;
                }
              }
          }
          @media (max-width: 992px){
              font-size: 20px;
          }
          &:hover {
            @media (min-width: 993px){
                &:after {
                  width: 100%;
                }
            }
          }
          @media (min-width: 993px){
            &:after {
              content: '';
              position: absolute;
              left: 0;
              bottom: -5px;
              width: 0%;
              height: 2px;
              background-color: var(--orange-color);
              transition: all 0.3s ease 0s;
            }
          }
        }
      }
      > li:nth-child(2) {
        position: relative;
        > a {
          &:hover {
            &:after {
              content: none;
            }
          }
        }
        > svg {
          transition: all 0.3s ease 0s;
          transform-origin: 50% 20%;
        }
        @media (min-width: 993px){
            &:hover {
              > ul {
                opacity: 1;
                visibility: visible;
              }
              > svg {
                transform: rotate(180deg);
              }
            }
        }
        svg {
          transform: translateY(-3px);
          margin-left: 5px;
          @media (max-width: 992px){
              display: none;
          }
        }
        > ul {
          position: absolute;
          left: -5px;
          top: 100%;
          width: 190px;
          background-color: var(--light-color);
          padding: 15px;
          border-radius: 7px;
          border: 1px solid rgba(#086470, 0.5);
          opacity: 0;
          visibility: hidden;
          transition: opacity 0.3s ease 0s, visibility 0.3s ease 0s;
          box-shadow: 0 0 12px 1px rgba(#086470, 0.35);
          @media (max-width: 992px){
              padding: 0;
              position: static;
              opacity: 1;
              visibility: visible;
              background-color: transparent;
              border: none;
              text-align: center;
              width: auto;
              padding: 5px 0 0 0;
              box-shadow: none;
          }
          > li {
            &:not(:last-child) {
              margin-bottom: 7px;
              @media (max-width: 992px){
                  margin-bottom: 4px;
              }
            }
            > a {
              color: var(--accent-color);
              font-family: 'Inter', sans-serif;
              font-size: 14px;
              font-style: normal;
              font-weight: 400;
              line-height: 125%;
              transition: all 0.3s ease 0s;
              text-transform: uppercase;
              @media (max-width: 992px){
                  font-size: 16px;
              }
              &:hover {
                color: var(--cta-color);
              }
            }
          }
        }
     }
  }
  @media (max-width: 992px){
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    border-radius: 4px;
    transform: translateY(-100%);
    transition: transform 0.3s ease 0s;
    background-image: linear-gradient(180deg,  rgba(177, 154, 154, 0.2) 0%, rgba(6, 80, 89, 0.63) 110%);
    background-color: var(--light-color);
    > ul {
      width: 100%;
      height: 100%;
    }
    &.menu--active {
      transform: translateY(0%);
      ~ .burger--active {
        color: var(--accent-color);
      }
      }
    }
  }

  .burger {
    display: none;
    @include burger;

    @media (max-width: 992px){
        display: block;
        z-index: 100;
    }
  }
}

// Header end ========================================================================================================================================================

// Hero section start ========================================================================================================================================================

.hero {
  padding-top: 158px;
  background-image: linear-gradient(180deg, rgba(8, 100, 112, 0.16) 0%, rgba(255, 255, 255, 0.00) 100%);
  overflow: hidden;

  // .hero__wrap

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1200px){
        flex-direction: column;
        row-gap: 50px;;
        align-items: stretch;
    }
    @media (max-width: 768px){
        row-gap: 30px;
    }
  }

  // .hero__content

  &__content {
    padding-top: 137px;
    flex: 0 1 calc(100% - 800px);
    @media (max-width: 1500px){
      flex: 0 1 calc(100% - 600px);
      padding-top: 0;
    }
    @media (max-width: 1200px){
        flex: 0 1 100%;
    }
  }

  // .hero__title

  &__title {
    background: linear-gradient(88deg, #223E4E -34.89%, #0091A4 123.19%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 13px;
    @media (max-width: 1400px){
        font-size: 60px;
    }
    @media (max-width: 768px){
        font-size: 50px;
    }
    @media (max-width: 480px){
        font-size: 32px;
    }
  }

  // .hero__subtitle

  &__subtitle {
    font-family: 'Inter', sans-serif;
    color: var(--main-color);
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    margin-bottom: 40px;
    max-width: 700px;
    @media (max-width: 1400px){
        font-size: 28px;
    }
    @media (max-width: 768px){
        font-size: 22px;
        margin-bottom: 30px;
    }
    @media (max-width: 480px){
        font-size: 18px;
    }
  }

  // .hero__btn

  &__btn {
    font-size: 18px;
    padding: 16px 45px;
    margin-bottom: 115px;
    @media (max-width: 1400px){
        padding: 20px 35px;
        margin-bottom: 80px;
    }
    @media (max-width: 1200px){
        margin-bottom: 40px;
    }
    @media (max-width: 768px){
        padding: 15px 30px;
    }
    @media (max-width: 480px){
        font-size: 14px;
    }
  }

  // .hero__items

  &__items {
    display: flex;
  }

  // .hero__item

  &__item {
    padding: 30px 40px;
    border-right: 1px solid rgba(#CBD4D6, 0.5);
    flex: 0 1 calc(100% / 3);
    text-align: center;
    &:first-child {
      border-left: 1px solid rgba(#CBD4D6, 0.5);
    }
    @media (max-width: 1400px){
        padding: 22px;
    }
    @media (max-width: 768px){
        padding: 15px;
    }
    @media (max-width: 480px){
        padding: 5px;
    }
  }

  // .hero__numbers

  &__numbers {
    font-family: 'Inter', sans-serif;
    color: var(--accent-color);
    font-size: 56px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%;
    @media (max-width: 1400px){
        font-size: 40px;
    }
    @media (max-width: 768px){
        font-size: 32px;
    }
    @media (max-width: 480px){
        font-size: 26px;
    }
  }

  // .hero__descr

  &__descr {
    font-family: 'Cabin', sans-serif;
    color: var(--main-color);
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 125%;
    @media (max-width: 768px){
        font-size: 14px;
    }
    @media (max-width: 480px){
        font-size: 12px;
    }
    @media (max-width: 480px){
      font-size: 10px;
    }
  }

  // .hero__globe

  &__globe {
    position: relative;
    flex: 0 1 800px;
    overflow: hidden;
    transform: translateX(110px);
    @media (max-width: 1700px){
      transform: translateX(0px);
    }
    @media (max-width: 1500px){
      flex: 0 1 600px;
    }
    @media (max-width: 1200px){
        flex: 0 1 100%;
        align-self: center;
    }
  }

  #markerLabel {
    width: 230px;
    height: 60px;
    color: #fffafa;
    font-family: 'Inter', sans-serif;
    font-size: 13px;
    padding: 5px;
    background: rgba(0, 0, 0, 0.75);
    border: 2px solid #fffafa;
    border-radius: 5px;
    margin-top: -25px;
    margin-left: 120px;
    //overflow: hidden;
  }
  .hidden{
    display: none;
  }
  .text {
    margin-left: 4px;
  }
  #closeButton{
    position: absolute;
    right: 0px;
    top: 0px;
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    border: 1px solid gray;
    border-radius: 3px;
    background: transparent;
    color: var(--light-color);
  }
}

// Hero section end ========================================================================================================================================================

// Section platform start ========================================================================================================================================================

.platform {
  padding-top: 330px;
  padding-bottom: 176px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center 30%;
  @media (max-width: 1600px){
      padding-top: 180px;
  }
  @media (max-width: 1200px){
      padding-top: 100px;
      padding-bottom: 100px;
  }
  @media (max-width: 768px){
      padding-top: 60px;
      padding-bottom: 60px;
  }

  // .platform__blocks

  &__blocks {
    position: relative;
    background-repeat: no-repeat;
    background-size: 245px 140px;
    background-position: left 220px;
  }

  // .platform__title

  &__title {
    position: absolute;
    left: 0;
    top: 47px;
    font-family: 'Inter', sans-serif;
    color: rgba(#086470, 0.7);
    font-size: 96px;
    font-style: normal;
    font-weight: 800;
    line-height: 100%; /* 96px */
    text-transform: uppercase;
    z-index: -1;
    @media (max-width: 1200px){
        position: static;
        font-size: 72px;
        margin-bottom: 40px;
        text-align: center;
    }
    @media (max-width: 576px){
        font-size: 42px;
    }
  }

  // .platform__block

  &__block {
    display: flex;
    column-gap: 9px;
    margin-bottom: 28px;
    @media (max-width: 992px){
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-bottom: 10px !important;
    }
    &:hover {
      .platform__img {
        img {
          transform: scale(1.1);
        }
      }
      .platform__icon {
        transform: rotateY(180deg);
      }
      .platform__icon svg {
        path {
          filter: invert(100%);
        }
      }
    }
    &_1 {
      justify-content: flex-end;
      padding-right: 250px;
      margin-bottom: -10px;
      .platform__svg {
        transform: translateY(-15px);
      }
      @media (max-width: 1500px){
          padding-right: 0;
      }
      @media (max-width: 1200px){
          margin-bottom: 0;
          justify-content: center;
          .platform__svg {
            transform: translateY(0px);
          }
      }
      @media (max-width: 992px){
          flex-direction: column-reverse;
          .platform__card {
            display: flex;
            flex-direction: column-reverse;
            row-gap: 24px;
          }
      }
      &:hover {
        svg circle {
          transform-origin: 100% 0%;
          animation: svgAnimationForStepOne 3s linear 0s infinite forwards;
        }
      }
    }
    &_2 {
      padding-left: 145px;
      margin-bottom: -25px;
      .platform__svg {
        transform: translateY(-25px);
      }
      @media (max-width: 1400px){
          padding-left: 0;
      }
      @media (max-width: 1200px){
          margin-bottom: 0;
          .platform__svg {
            transform: translateY(0px);
          }
      }
      &:hover {
        svg circle {
          transform-origin: 100% 0%;
          animation: svgAnimationForStepTwo 3s linear 0s infinite forwards;
        }
      }
      .platform__img {
        margin-top: 24px;
      }
    }
    &_3 {
      justify-content: flex-end;
      margin-bottom: -25px;
      .platform__svg {
        transform: translateY(-35px);
      }
      @media (max-width: 1400px){
          .platform__descr {
            text-align: right;
            @media (max-width: 992px){
                text-align: left;
            }
          }
      }
      @media (max-width: 1200px){
          margin-bottom: 0;
        .platform__svg {
          transform: translateY(0px);
        }
      }
      @media (max-width: 992px){
        flex-direction: column-reverse;
        .platform__card {
          display: flex;
          flex-direction: column;
          row-gap: 24px;
        }
      }
      &:hover {
        svg circle {
          transform-origin: 100% 0%;
          animation: svgAnimationForStepThree 3s linear 0s infinite forwards;
        }
      }
      .platform__img {
        margin-top: 24px;
        @media (max-width: 992px){
            margin-top: 0;
        }
      }
    }
    &_4 {
      padding-left: 275px;
      .platform__card {
        padding-bottom: 120px;
      }
      @media (max-width: 1500px){
          padding-left: 0;
      }
      @media (max-width: 1200px){
        .platform__card {
          padding-bottom: 0px;
        }
      }
      @media (max-width: 1200px){
        .platform__svg {
          text-align: left;
        }
        svg {
          width: 123px !important;
        }
      }
      @media (max-width: 992px){
        .platform__card {
          display: flex;
          flex-direction: column-reverse;
          row-gap: 24px;
        }
      }
      &:hover {
        svg circle {
          transform-origin: 100% 0%;
          animation: svgAnimationForStepFour 3s linear 0s infinite forwards;
        }
      }
    }
  }

  // .platform__svg

  &__svg {
    @media (max-width: 992px){
      position: relative;
      &:after {
        content: '' ;
        position: absolute;
        top: 0;
        left: 1px;
        border-radius: 50%;
        width: 10px;
        height: 10px;
        background-color:var(--orange-color);
        animation: svgAnimationForTablet 2s linear 0s infinite forwards;
      }
    }
    > img {
      display: none;
    }
    align-self: flex-end;
    @media (max-width: 1200px){
      flex: 0 1 calc(100% - 509px);
      justify-self: flex-end;
      text-align: right;
      svg {
        width: 100%;
      }
    }
    @media (max-width: 992px){
        flex: 0 1 100%;
        align-self: center;
        svg {
          display: none;
        }
        img {
          display: block;
       }
    }
  }

  // .platform__card

  &__card {
    @media (max-width: 1200px){
      flex: 0 1 500px;
    }
    @media (max-width: 992px){
        flex: 0 1 100%;
        align-items: center;
    }
  }

  // .platform__img

  &__img {
    border-radius: 12px;
    margin-bottom: 24px;
    max-width: 485px;
    max-height: 320px;
    overflow: hidden;
    img {
      transition: all 0.6s ease 0s;
      width: 100%;
      height: 100%;
    }
  }

  // .platform__descr

  &__descr {
    display: flex;
    align-items: center;
    column-gap: 20px;
    @media (max-width: 400px){
      align-items: flex-start;
      column-gap: 10px;
    }
  }

  // .platform__icon

  &__icon {
    flex: 0 1 96px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    border: 1px solid rgba(203, 212, 214, 0.50);
    background: var(--light-color);
    svg {
      path {
        transition: all 0.5s ease 0.3s;
      }
    }
    transition: all 0.5s ease 0.3s;
    @media (max-width: 576px){
        flex: 0 1 70px;
        width: 70px;
        height: 70px;
        svg {
          width: 40px;
          height: 40px;
        }
    }
    @media (max-width: 400px){
      flex: 0 1 50px;
      width: 50px;
      height: 50px;
      svg {
        width: 30px;
        height: 30px;
      }
    }
  }

  // .platform__names

  &__names {
    flex: 0 1 calc(100% - 116px);
    @media (max-width: 576px){
      flex: 0 1 calc(100% - 90px)
    }
    @media (max-width: 576px){
      flex: 0 1 calc(100% - 60px)
    }
    h4 {
      font-family: 'Inter', sans-serif;
      color: var(--accent-color);
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 120%;
      margin-bottom: 16px;
      @media (max-width: 1400px){
        max-width: 400px;
          br {
            display: none;
          }
      }
      @media (max-width: 576px){
          font-size: 22px;
          margin-bottom: 5px;
      }
      @media (max-width: 400px){
          font-size: 18px;
      }
    }
    p {
      font-family: 'Cabin', sans-serif;
      color:rgba(#293545, 0.7);
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 125%;
      max-width: 365px;
      @media (max-width: 576px){
          font-size: 14px;
      }
    }
  }

  .platform-solutions {

    padding-top: 78px;
    @media (max-width: 1200px){
        padding-top: 20px;
    }

    // .platform-solutions__items

    &__items {
      max-width: 1200px;
      margin: 0 auto;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-auto-rows: 1fr;
      gap: 15px;
      @media (max-width: 992px){
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(4, 300px);
      }
      @media (max-width: 576px){
        grid-template-columns: 1fr;
        grid-template-rows: repeat(6, 280px);
        grid-auto-rows: 280px;
      }
      @media (max-width: 480px){
        grid-template-rows: repeat(6, 250px);
        grid-auto-rows: 250px;
      }
    }

    // .platform-solutions__item

    &__item {
      &:not(:nth-child(2)) {
        background-color: var(--accent-color);
        border-radius: 12px;
        overflow: hidden;
        padding: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-image: url('../img/bg-logo-platform-sm.svg');
        background-repeat: no-repeat;
        //background-size: 149px 85px;
        //background-position: 32px 32px;
        background-position: 20px 20px;
        background-size: 100px auto;
        transition: all 0.3s ease 0s;
        @media (max-width: 1400px){
          //background-position: 20px 20px;
          //background-size: 100px auto;
          //padding: 25px;
        }
        @media (max-width: 992px){
            padding: 20px;
            justify-content: justify-content;
            div:nth-child(2) {
              min-height: 80px;
              @media (max-width: 480px){
                  min-height: auto;
              }
            }
        }
        @media (max-width: 480px){
          background-size: 60px auto;
          background-position: 15px 15px;
        }
        &:hover {
          box-shadow: 1px 1px 25px 5px rgba(#086470, 0.7);
          background-position: 22px 22px;
