            margin-top: 25px;
            width: 100%;
            font-size: 18px;
            padding: 10px 0;
        }
      }
    }
  }

  // Checkbox

  &__label-checkbox {
    cursor: pointer;
    font-family: 'Cabin', sans-serif;
    color: rgba(#ffffff, 0.5);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 125%;
    @media (max-width: 480px){
        font-size: 12px;
        line-height: 18px;
    }
    input {
      width: 0px;
      height: 0px;
      opacity: 0;
      display: none;
      &:checked + span:after {
        opacity: 1;
      }
    }
    > span {
      float: left;
      display: inline-block;
      transform: translateY(2px);
      width: 20px;
      height: 20px;
      border: 1px solid rgba(#ffffff, 0.5);
      border-radius: 4px;
      position: relative;
      margin-right: 12px;
      margin-bottom: 45px;
      background-color: #417178;
      @media (max-width: 480px){
          margin-right: 5px;
          width: 16px;
          height: 16px;
      }
      &.dark-mode {
        &::after {
          border-color:var(--orange-color);
        }
      }
      &::after {
        content: '';
        position: absolute;
        top: calc(50% - 2px);
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        width: 8px;
        height: 12px;
        border: 2px solid var(--light-color-for-checkbox);
        border-left: none;
        border-top: none;
        background-color: transparent;
        border-radius: 2px;
        transition: all 0.3s ease 0s;
        opacity: 0;
        @media (max-width: 480px){
            top: calc(50% - 1px);
            width: 6px;
            height: 8px;
        }
      }
    }
    a {
      color: var(--light-color);
      opacity: 1;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

